




























































import { Component, Vue } from "vue-property-decorator";
import TheCanvas from "./components/TheCanvas.vue";

@Component({
  components: {
    TheCanvas
  }
})
export default class App extends Vue {
  clearDialog = false;
  resetDialog = false;
  map!: TheCanvas;

  clearIcons() {
    this.map.clearIcons();
    this.clearDialog = false;
  }
  resetIcons() {
    this.map.resetIcons();
    this.resetDialog = false;
  }
  mounted() {
    this.map = this.$refs.canvas as TheCanvas;
  }
}
