interface Vector2D {
  x: number;
  y: number;
}

export default class Matrix2D {
  public a = 1;
  public b = 0;
  public c = 0;
  public d = 1;
  public x = 0;
  public y = 0;

  public getInverse(): Matrix2D {
    const result = new Matrix2D();
    const det = this.a * this.d - this.b * this.c;
    result.a = this.d / det;
    result.b = -this.b / det;
    result.c = -this.c / det;
    result.d = this.a / det;
    result.x = (this.c * this.y - this.d * this.x) / det;
    result.y = (this.b * this.x - this.a * this.y) / det;
    return result;
  }

  public translate(vec: Vector2D): Matrix2D {
    const result = new Matrix2D();
    result.a = this.a;
    result.b = this.b;
    result.c = this.c;
    result.d = this.d;
    result.x = this.a * vec.x + this.c * vec.y + this.x;
    result.y = this.b * vec.x + this.d * vec.y + this.y;
    return result;
  }

  public scale(vec: Vector2D): Matrix2D {
    const result = new Matrix2D();
    result.a = this.a * vec.x;
    result.b = this.b * vec.x;
    result.c = this.c * vec.y;
    result.d = this.d * vec.y;
    result.x = this.x;
    result.y = this.y;
    return result;
  }
  public transformPoint(vec: Vector2D): Vector2D {
    return {
      x: this.a * vec.x + this.c * vec.y + this.x,
      y: this.b * vec.x + this.d * vec.y + this.y
    };
  }
}
