



























/// <reference types="resize-observer-browser"/>
import { Component, Vue, Provide } from "vue-property-decorator";
import IconMenu from "./IconMenu.vue";
import CanvasViewport from "../CanvasViewport";
import { IconManager } from "../IconManager";

interface Icon {
  x: number,
  y: number,
  imageName: string
}

@Component({
  components: {
    IconMenu
  }
})
export default class TheCanvas extends Vue {
  canvas!: HTMLCanvasElement;
  context!: CanvasRenderingContext2D;
  container!: Element;
  lightWorldImage!: HTMLImageElement;
  darkWorldImage!: HTMLImageElement;
  viewport: CanvasViewport = new CanvasViewport();

  @Provide()
  iconManager = new IconManager();

  icons = Array<Icon>();
  iconWidth = 24;

  showIconMenu = false;
  menuPoint = { x: 0, y: 0 };
  currentPoint = { x: 0, y: 0 };
  currentIcon: Icon | undefined;
  isDragging = false;
  editMode = false;
  offset = "top-left";
  dragStart = {x: 0, y: 0};

  draw() {
    this.context.resetTransform();
    this.context.clearRect(0, 0, this.viewport.width, this.viewport.height);
    const mapWidth = 500;
    const m = this.viewport.getMatrix().getInverse();
    this.context.setTransform(m.a, m.b, m.c, m.d, m.x, m.y);
    this.context.drawImage(this.lightWorldImage, 0, 0, mapWidth, mapWidth);
    this.context.drawImage(
      this.darkWorldImage,
      mapWidth,
      0,
      mapWidth,
      mapWidth
    );
    const realWidth = this.iconWidth * this.viewport.scale;
    this.icons.forEach(icon => {
      const img = this.iconManager.getImage(icon.imageName);
      if (img !== undefined && img.complete) {
        this.context.drawImage(
          img,
          icon.x - realWidth,
          icon.y - realWidth,
          realWidth * 2,
          realWidth * 2
        );
      }
    });
  }

  onResize() {
    this.viewport.width = this.container.clientWidth;
    this.viewport.height = this.container.clientHeight;
  }

  mounted() {
    this.canvas = this.$refs.canvas as HTMLCanvasElement;
    this.context = this.canvas.getContext("2d") as CanvasRenderingContext2D;
    this.container = this.$refs.container as Element;
    this.lightWorldImage = new Image();
    this.lightWorldImage.src = require("../assets/alttp_lightworld.png");
    this.lightWorldImage.addEventListener("load", this.draw, { once: true });
    this.darkWorldImage = new Image();
    this.darkWorldImage.src = require("../assets/alttp_darkworld.png");
    this.darkWorldImage.addEventListener("load", this.draw, { once: true });
    this.onResize();

    const observer = new ResizeObserver(entries => {
      entries.forEach(entry => {
        this.onResize();
      })
    })
    observer.observe(this.$refs.container as Element);

    const iconsStr = window.localStorage.getItem("icons");
    if (iconsStr !== null) {
      this.icons = JSON.parse(iconsStr);
    } else {
      this.icons = this.getCopyOfDefaultIcons();
      window.localStorage.setItem('icons', JSON.stringify(this.icons));
    }
  }

  onWheel(event: MouseWheelEvent) {
    if (event.deltaY > 0) {
      this.viewport.zoomPoint(event.offsetX, event.offsetY, 1.2);
      this.showIconMenu = false;
    } else if (event.deltaY < 0) {
      this.viewport.zoomPoint(event.offsetX, event.offsetY, 1 / 1.2);
      this.showIconMenu = false;
    }
    this.draw();
  }

  onMove(event: MouseEvent) {
    const distSquared = Math.pow(event.offsetX - this.dragStart.x, 2)
      + Math.pow(event.offsetY - this.dragStart.y, 2);
    if (event.buttons === 1 && distSquared >= 25) {
      this.viewport.translate(-event.movementX, -event.movementY);
      this.showIconMenu = false;
      this.isDragging = true;
      this.draw();
    }
  }

  onMouseDown(event: MouseEvent) {
    this.isDragging = false;
    this.dragStart = {
      x: event.offsetX,
      y: event.offsetY
    };
  }

  onMouseUp(event: MouseEvent) {
    if (!this.isDragging) {
      let horz = "left";
      let vert = "bottom";
      if (event.offsetY > this.viewport.height / 2) {
        vert = "bottom";
        this.menuPoint.y = this.viewport.height - event.offsetY;
      } else {
        vert = "top";
        this.menuPoint.y = event.offsetY;
      }
      if (event.offsetX > this.viewport.width / 2) {
        horz = "right";
        this.menuPoint.x = this.viewport.width - event.offsetX;
      } else {
        horz = "left";
        this.menuPoint.x = event.offsetX;
      }
      this.offset = `${vert}-${horz}`;

      this.currentPoint = this.viewport.toWorld({
        x: event.offsetX,
        y: event.offsetY
      });
      this.editMode = false;
      const getIcon = this.getIconAt({ x: event.offsetX, y:event.offsetY });
      if (getIcon) {
        this.currentIcon = getIcon;
        this.editMode = true;
      }
      this.showIconMenu = true;
    }
  }

  getIconAt(point: { x: number, y: number }) {
    const worldPoint = this.viewport.toWorld(point);
    const realWidth = this.iconWidth * this.viewport.scale;
    let result;
    this.icons.forEach(icon => {
      const minx = icon.x - realWidth / 2;
      const miny = icon.y - realWidth / 2;
      const maxx = icon.x + realWidth / 2;
      const maxy = icon.y + realWidth / 2;
      if (minx < worldPoint.x && worldPoint.x < maxx
        && miny < worldPoint.y && worldPoint.y < maxy) {
        result = icon;
      }
    });
    return result;
  }

  updated() {
    this.draw();
  }

  onAddIcon(imageName: string) {
    this.icons.push({
      x: this.currentPoint.x,
      y: this.currentPoint.y,
      imageName: imageName
    });
    window.localStorage.setItem("icons", JSON.stringify(this.icons));
    this.showIconMenu = false;
    this.draw();
  }

  onChangeIcon(imageName: string) {
    this.showIconMenu = false;
    this.currentIcon!.imageName = imageName;
    window.localStorage.setItem("icons", JSON.stringify(this.icons));
  }

  onDeleteIcon() {
    this.showIconMenu = false;
    this.icons = this.icons.filter(icon => icon !== this.currentIcon);
    window.localStorage.setItem("icons", JSON.stringify(this.icons));
  }

  clearIcons() {
    this.icons = [];
    window.localStorage.setItem("icons", JSON.stringify(this.icons));
    this.draw();
  }

  resetIcons() {
    this.icons = this.getCopyOfDefaultIcons();
    window.localStorage.setItem("icons", JSON.stringify(this.icons));
    this.draw();
  }

  private getCopyOfDefaultIcons() {
    return (require("../assets/default.json") as Icon[]).map(item => ({...item}));
  }
}
