export class IconManager {
  private _images: Map<string, HTMLImageElement> = new Map();
  private _names: string[];

  public constructor() {
    this._names = [];
    const colors = [
      "red",
      "orange",
      "yellow",
      "green",
      "blue",
      "magenta"
    ];
    const shapes = [
      "square",
      "circle",
      "triangle",
      "inv_triangle",
      "spade",
      "club",
      "diamond",
      "heart"
    ];
    for (const color of colors) {
      for (const shape of shapes) {
        const name = `${color}_${shape}`;
        this._names.push(name);
      }
    }
    this._names = this._names.concat([
      "hc_l",
      "hc_d",
      "hc_r",
      "ep",
      "dp_u",
      "dp_d",
      "dp_l",
      "dp_r",
      "toh",
      "at",
      "pod",
      "swp",
      "skw",
      "tt",
      "ip",
      "mm",
      "tr_u",
      "tr_d",
      "tr_l",
      "tr_r",
      "gt",
      "gan",
      "mc_r",
      "mc_l",
      "snc",
      "shs",
      "lib",
      "smt",
      "kid",
      "bat",
      "bsh",
      "psh"
    ]);
    for (const i of this._names) {
      const name = i;
      const image = new Image();
      image.src = require(`./assets/icons/${name}.png`);
      this._images.set(name, image);
    }
  }

  public getImage(name: string): HTMLImageElement | undefined {
    return this._images.get(name);
  }

  public get names() {
    return this._names;
  }
}
