import Matrix2D from "./Matrix2D";

function clamp(num: number, min: number, max: number) {
  if (num < min) {
    return min;
  } else if (num > max) {
    return max;
  } else {
    return num;
  }
}

export default class CanvasViewport {
  private _width = 500;
  private _height = 500;
  private _x = 0;
  private _y = 0;
  private _scale = 1;
  private _xMax = 1000;
  private _xMin = 0;
  private _yMax = 500;
  private _yMin = 0;
  public constrainScale = true;
  public constrainPosition = true;
  private _minScale = 1 / 40;

  public get width() {
    return this._width;
  }
  public set width(width) {
    this._width = width;
    this.constrain();
  }

  public get height() {
    return this._height;
  }
  public set height(height) {
    this._height = height;
    this.constrain();
  }

  public get x() {
    return this._x;
  }
  public set x(x) {
    this._x = x;
    this.constrain();
  }

  public get y() {
    return this._y;
  }
  public set y(y) {
    this._y = y;
    this.constrain();
  }

  public get scale() {
    return this._scale;
  }
  public set scale(scale) {
    this._scale = scale;
    this.constrain();
  }

  public get minScale() {
    return this._minScale;
  }

  public set minScale(minScale) {
    this._minScale = minScale;
    this.constrain();
  }

  public translate(x: number, y: number) {
    this._x += x * this._scale;
    this._y += y * this._scale;
    this.constrain();
  }

  public zoomPoint(x: number, y: number, factor: number) {
    const oldScale = this._scale;
    this._scale *= factor;
    this._constrainScale();
    const actualFactor = this._scale / oldScale;
    this._x += x * oldScale * (1 - actualFactor);
    this._y += y * oldScale * (1 - actualFactor);
    this._constrainPosition();
  }

  public get xMax() {
    return this._xMax;
  }
  public set xMax(xMax) {
    this._xMax = xMax;
    this.constrain();
  }
  public get xMin() {
    return this._xMin;
  }
  public set xMin(xMin) {
    this._xMin = xMin;
    this.constrain();
  }
  public get yMax() {
    return this._yMax;
  }
  public set yMax(yMax) {
    this._yMax = yMax;
    this.constrain();
  }
  public get yMin() {
    return this._yMin;
  }
  public set yMin(yMin) {
    this._yMin = yMin;
    this.constrain();
  }

  public getMatrix() {
    return new Matrix2D()
      .translate({ x: this._x, y: this._y })
      .scale({ x: this._scale, y: this.scale });
  }

  public toWorld(screenCoord: { x: number; y: number }) {
    const mat = this.getMatrix();
    return mat.transformPoint(screenCoord);
  }

  private _constrainScale() {
    if (this.constrainScale) {
      const xMaxScale = (this._xMax - this._xMin) / this._width;
      const yMaxScale = (this._yMax - this._yMin) / this._height;
      this._scale = clamp(this._scale, 0.025, Math.max(xMaxScale, yMaxScale));
    }
  }

  private _constrainPosition() {
    if (this.constrainPosition) {
      const boundsWidth = this._xMax - this._xMin;
      const boundsHeight = this._yMax - this._yMin;
      const viewWidth = this._width * this._scale;
      const viewHeight = this._height * this._scale;
      if (boundsWidth <= viewWidth) {
        this._x = (boundsWidth - viewWidth) / 2;
      } else {
        this._x = clamp(this._x, this._xMin, this._xMax - viewWidth);
      }
      if (boundsHeight <= viewHeight) {
        this._y = (boundsHeight - viewHeight) / 2;
      } else {
        this._y = clamp(this._y, this._yMin, this._yMax - viewHeight);
      }
    }
  }

  private constrain() {
    this._constrainScale();
    this._constrainPosition();
  }
}
