























import { Component, Vue, Prop, Inject } from "vue-property-decorator";
import { IconManager } from "../IconManager";
type Offset = "top-left" | "top-right" | "bottom-left" | "bottom-right";

@Component
export default class IconMenu extends Vue {
  @Prop({ type: Number, default: 10 }) readonly x!: number;
  @Prop({ type: Number, default: 10 }) readonly y!: number;
  @Prop({ type: Boolean, default: false }) readonly editMode!: boolean;
  @Prop({ type: String, default: "top-left" }) readonly offset!: Offset;

  @Inject() readonly iconManager!: IconManager;

  getImgSrc(src: string) {
    return require(`../assets/buttons/${src}.png`);
  }

  get styleObj() {
    switch (this.offset) {
      case "top-left":
        return {
          top: `${this.y + 10}px`,
          left: `${this.x + 10}px`
        };
      case "top-right":
        return {
          top: `${this.y + 10}px`,
          right: `${this.x + 10}px`
        };
      case "bottom-left":
        return {
          bottom: `${this.y + 10}px`,
          left: `${this.x + 10}px`
        };
      case "bottom-right":
        return {
          bottom: `${this.y + 10}px`,
          right: `${this.x + 10}px`
        };
      default:
        return {
          top: "0px",
          left: "0px"
        };
    }
  }
}
